@import 'foundation';
:root {
  --max-width: 112.5rem;
}
html {
  font-size: 1em;
  font-family: CenturyGothic, Helvetica, Arial, sans-serif;
  color: black;
}
body {
  background-image: url("/images/bg/txtr.jpg");
}
body a {
  color: #A63624;
}
#logoRow {
  max-width: var(--max-width);
  margin: 0 auto;
  width: 100%;
  min-height: 6rem;
  background-color: #373627;
  color: white;
  padding: 1em;
  display: grid;
  grid-template-columns: 1fr 4fr 1fr;
  align-items:center;
}
#logoRow label {
  font-size: 2rem;
}
.navToggle {
  display: none;
}
#joinLink {
  display: none;
}
#logoLink {
  justify-self: center;
}
#cartLinkMinimal {
  justify-self: right;
}
#logoRowCartPhone {
  display: none;
}
#navPrimarySmall {
  display: none;
  max-width: var(--max-width);
  width: 100%;
  margin: 0;
  padding: .4em 0;
  background-color: #373627;
  grid-column: span 3;
}
#navPrimarySmall ul li {
  text-transform: capitalize;
  color: #fff;
  padding: .4em 2em;
}
#navPrimarySmall ul li a, #navPrimarySmall ul li a:visited {
  color: #fff;
}
#navPrimarySmall ul li a:hover {
  color: #373627;
  background-color: #fff;
}
.navToggle:checked ~ #navPrimarySmall {
  display: block;
}
li {
  list-style: none;
}
#searchLabel {
  display: none;
}
.navPrimaryMedia a img {
  background-color: white;
  display: inline;
}
#navPrimary {
  display: none;
  max-width: var(--max-width);
  margin: 0 auto;
  background-color: #c97737;
  padding: .4rem 0;
}
.navList {
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}
.navList li {
  padding: .5rem;
}
.navPrimaryLink a, .navPrimaryLink a:visited {
  padding: .3rem;
  color: #1e1e1b;
  text-decoration: none;
  font-size: .9rem;
  font-weight: 500;
  text-transform: uppercase;
}
.navPrimaryLink a:hover {
  background-color: white;
}
.navPrimarySearch form {
  display: inline-flex;
  justify-content: center;
  margin: 0;
  padding: 0;
}
.navPrimarySearch input {
  border: 1px solid #373627;
  margin-right: .2rem;
  padding-left: .3rem;
}
.navPrimarySearch button {
  text-decoration: none;
  margin: 0;
  border: 0;
  background-color: transparent;
  appearance: none;
}
.navPrimarySearch button:hover {
  cursor: pointer;
}
main {
  max-width: var(--max-width);
  margin: 0 auto;
  background-color: white;
  padding: 0 1em;
  border: 1px solid white;
}
#footerPrimary {
  display: none;
  max-width: var(--max-width);
  margin: 0 auto;
  background-color: #DCDCDB;
  padding: .4rem 0 .2rem;
}
#footerPrimary img {
  display: inline;
  border: 0;
}
#footerPrimary li a {
  padding: .3rem;
  color: #373627;
  text-decoration: none;
  font-size: .9rem;
  font-weight: 500;
  text-transform: uppercase;
}
#footerPrimary li a:hover {
  background-color: white;
}
#copyright {
  margin-top: .4em;
  font-size: .75rem;
  color: #383730;
  text-align: center;
}
#copyright a {
  color: #383730;
  text-decoration: underline;
}

@media only screen and (min-width: 810px) {
  #logoRow label {
    display: none;
  }
  #joinLink {
    display: block;
  }
  #cartLinkMinimal {
    display: none;
  }
  #logoRowCartPhone {
    display: block;
  }
  .navToggle:checked ~ #navPrimarySmall {
    display: none;
  }
  #navPrimarySmall {
    display: none;
  }
  #navPrimary {
    display: block;
  }
  #footerPrimary {
    display: block;
  }
}